import { Trans, useTranslation } from 'react-i18next';
import style from './sustainSection.module.scss';
import topImg1 from './img/top.webp';
import topImg2 from './img/top.jpg';
import bottomImg1 from './img/bottom.webp';
import bottomImg2 from './img/bottom.jpg';
import { Collapse } from 'react-collapse';
import { useEffect, useState } from 'react';

export default function SustainSection() {
  const [openText, setOpenText] = useState(0);
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);
  const {t} = useTranslation();
  const items = [
    {
      heading: 'aboutpage.sustainSection.top.item1.heading',
      text: 'aboutpage.sustainSection.top.item1.text',
    },
    {
      heading: 'aboutpage.sustainSection.top.item2.heading',
      text: 'aboutpage.sustainSection.top.item2.text',
    },
    {
      heading: 'aboutpage.sustainSection.top.item3.heading',
      text: 'aboutpage.sustainSection.top.item3.text',
    },
  ]

  useEffect(() => {
    const handleResize = () => {
      setWindowWidth(window.innerWidth);
    }
    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
    }
  }, [])

  return (
    <section className={style.section}>
      <div className="container">
        <div className={style.top}>
          <div className={style.top__sustain}>{t('aboutpage.sustainSection.top.sustain')}</div>
          <h3 className={style.top__heading}>{t('aboutpage.sustainSection.top.heading')}</h3>
          <div className={style.top__wrap}>
            <div className={style.top__img}>
              <picture>
                <source src={topImg1} type='image/webp' />
                <img src={topImg2} alt="bicyclists and mountain" loading='lazy' decoding='async'/>
              </picture>
            </div>
            <div className={style.top__content}>
              {items.map((item,index) => (
                <div key={index} className={style.top__contentBlock}>
                  <h4 onClick={windowWidth >= 768 ? undefined : () => setOpenText(openText === index + 1 ? 0 : index + 1)} className={style.top__contentHeading}>{t(item.heading)}</h4>
                  <Collapse isOpened={ windowWidth >= 768  || openText === index + 1}>
                    <p className={style.top__contentText}>
                      <Trans i18nKey={item.text}>
                        t<a target='_blank' href='https://www.changeclimate.org/?_gl=1*g5bh1b*_gcl_aw*R0NMLjE2ODY4MTU1NjIuQ2owS0NRanc3YXFrQmhEUEFSSXNBS0dhMG9KRlg0b1FONzNWOHhzZGpielNLenl3eXdwRDNaZHkyaWxHOU1CNFQzTXpCUS14TGVTcm1UTWFBdWo5RUFMd193Y0I.*_gcl_au*MTIxMjA3Mzc2NS4xNjg2MDY3Njc5'></a>
                        <a target='_blank' href='https://how2recycle.info/?_gl=1*1wrx9d3*_gcl_aw*R0NMLjE2ODY4MTU1NjIuQ2owS0NRanc3YXFrQmhEUEFSSXNBS0dhMG9KRlg0b1FONzNWOHhzZGpielNLenl3eXdwRDNaZHkyaWxHOU1CNFQzTXpCUS14TGVTcm1UTWFBdWo5RUFMd193Y0I.*_gcl_au*MTIxMjA3Mzc2NS4xNjg2MDY3Njc5'></a>
                      </Trans>
                    </p>
                  </Collapse>
                </div>
              ))}
            </div>
          </div>
        </div>
        <div className={style.bottom}>
          <div className={style.bottom__content}>
            <h3 className={style.bottom__heading}>{t('aboutpage.sustainSection.bottom.heading')}</h3>
            <p className={style.bottom__text}>{t('aboutpage.sustainSection.bottom.text')}</p>
          </div>
          <div className={style.bottom__img}>
            <picture>
              <source src={bottomImg1} />
              <img src={bottomImg2} alt="laughing girls" loading='lazy' decoding='async'/>
            </picture>
          </div>
        </div>
      </div>
    </section>
  )
}