import { useState} from 'react';
import { createPortal } from 'react-dom';
import ModalLang from './ModalLang/ModalLang';

interface LangSwitcherProps {
  children: JSX.Element
}

export default function LangSwitcher({children}:LangSwitcherProps) {
  const [isSwitcherOpen, setIsSwitcherOpen] = useState(false);

  function setPortal(active:boolean) {
    let body = document.querySelector('body');
    if (body) {
      body.style.marginRight = body.classList.contains('portal') ?
        '0'
        :
        window.innerWidth - body.clientWidth + 'px';
      if (active) {
        body.classList.add('portal');
      } else {body.classList.remove('portal')}
    }
    setIsSwitcherOpen(active);
  }
  
  return (
    <>
      <div onClick={() => setPortal(true)}>
        {children}
      </div>
      {isSwitcherOpen && createPortal(<ModalLang setPortal={setPortal} />, document.body)}
    </>
  )
}