import { useTranslation } from 'react-i18next';
import style from './aboutSection.module.scss';
import img11 from './img/img1.webp';
import img12 from './img/img1.jpg';
import img21 from './img/img2.webp';
import img22 from './img/img2.jpg';
import img31 from './img/img3.webp';
import img32 from './img/img3.jpg';

export default function AboutSection() {
  const {t} = useTranslation()
  const content = [
    {
      heading: 'aboutpage.aboutSection.item1.heading',
      text: ['aboutpage.aboutSection.item1.p1'],
      img1: img11,
      img2: img12,
      alt: 'bicyclist',
    },
    {
      heading: 'aboutpage.aboutSection.item2.heading',
      text: ['aboutpage.aboutSection.item2.p1'],
      img1: img21,
      img2: img22,
      alt: 'glass of AG1',
    },
    {
      heading: 'aboutpage.aboutSection.item3.heading',
      text: ['aboutpage.aboutSection.item3.p1', 'aboutpage.aboutSection.item3.p2'],
      img1: img31,
      img2: img32,
      alt: 'factory of AG1',
    },
  ]

  return (
    <section className={style.section}>
      <div className="container">
        {content.map((item, index) => (
          <div key={index} className={style.item}>
            <div className={style.content}>
              <h2 className={style.heading}>{t(item.heading)}</h2>
              {item.text.map((item, index) => (
                <p key={index} className={style.text}>{t(item)}</p>
              ))}
            </div>
            <div className={style.img}>
              <picture>
                <source src={item.img1}/>
                <img src={item.img2} alt={item.alt} decoding='async' loading='lazy' />
              </picture>
            </div>
          </div>
        ))}
      </div>
    </section>
  )
}