import { useTranslation } from 'react-i18next';
import style from './sloganSection.module.scss';
import bg1 from './img/bg.webp';
import bg2 from './img/bg.jpg';

export default function SloganSection() {
  const {t} = useTranslation()

  return (
    <section className={style.section}>
      <div className={style.imgWrap}>
        <div className={style.img}>
          <picture>
            <source type='image/webp' src={bg1} />
            <img src={bg2} alt="background" loading='lazy' decoding='async' />
          </picture>
        </div>
      </div>
      <h1 className={style.heading}>{t('aboutpage.sloganSection.heading')}</h1>
    </section>
  )
}