import style from './shipping.module.scss';
import { InputText } from 'primereact/inputtext';
import {useState, ChangeEvent, useRef} from 'react';
import { useTranslation } from 'react-i18next';
import { ReactComponent as Icon } from './img/icon2.svg' ;
import { Dropdown, DropdownChangeEvent } from 'primereact/dropdown';
import { getProduct } from './../content';
import { apiKey, noProxyUrl, url } from '../../../mail';
import { useNavigate, useParams } from 'react-router';
import {createPortal} from 'react-dom'
import { PATHS } from '../../../App';

export default function Shipping() {
  const {t} = useTranslation();
  const countries = [
    { name: t('cart.shipping.countries.item0') },
    { name: t('cart.shipping.countries.item1') },
    { name: t('cart.shipping.countries.item2') },
    { name: t('cart.shipping.countries.item3') },
    { name: t('cart.shipping.countries.item4') },
    { name: t('cart.shipping.countries.item5') },
    { name: t('cart.shipping.countries.item6') },
    { name: t('cart.shipping.countries.item7') },
    { name: t('cart.shipping.countries.item8') },
    { name: t('cart.shipping.countries.item9') },
    { name: t('cart.shipping.countries.item10') },
    { name: t('cart.shipping.countries.item11') },
    { name: t('cart.shipping.countries.item12') },
    { name: t('cart.shipping.countries.item13') },
    { name: t('cart.shipping.countries.item14') },
    { name: t('cart.shipping.countries.item15') },
    { name: t('cart.shipping.countries.item16') },
    { name: t('cart.shipping.countries.item17') },
    { name: t('cart.shipping.countries.item18') },
    { name: t('cart.shipping.countries.item19') },
    { name: t('cart.shipping.countries.item20') },
    { name: t('cart.shipping.countries.item21') },
    { name: t('cart.shipping.countries.item22') },
    { name: t('cart.shipping.countries.item23') },
    { name: t('cart.shipping.countries.item24') },
    { name: t('cart.shipping.countries.item25') },
    { name: t('cart.shipping.countries.item26') },
    { name: t('cart.shipping.countries.item27') },
    { name: t('cart.shipping.countries.item28') },
    { name: t('cart.shipping.countries.item29') },
    { name: t('cart.shipping.countries.item31') },
    { name: t('cart.shipping.countries.item32') },
    { name: t('cart.shipping.countries.item33') },
    { name: t('cart.shipping.countries.item34') },
    { name: t('cart.shipping.countries.item35') },
    { name: t('cart.shipping.countries.item36') },
    { name: t('cart.shipping.countries.item37') },
    { name: t('cart.shipping.countries.item38') },
    { name: t('cart.shipping.countries.item39') },
  ];
  const navigate = useNavigate();
  const param = useParams();
  const product = getProduct(param.product, navigate);
  const subRef = useRef<HTMLInputElement>(null)
  const [isSubLoading, setIsSubLoading] = useState(false);
  const [isSendLoading, setIsSendLoading] = useState(false);
  const [isSuccess, setIsSuccess] = useState(false);
  const [selectedCountry, setSelectedCountry] = useState<{name: '',}|null>(null);
  const [emailInput, setEmailInput] = useState('');
  const [errors, setErrors] = useState({
    email: '',
    country: '',
  })
  const [fields, setFields] = useState([
    {
      id: 'first',
      label: 'cart.shipping.firstName',
      isRequired: true,
      value: '',
      error: '',
    },
    {
      id: 'last',
      label: 'cart.shipping.lastName',
      isRequired: true,
      value: '',
      error: '',
    },
    {
      id: 'addres',
      label: 'cart.shipping.address',
      isRequired: true,
      value: '',
      error: '',
    },
    {
      id: 'apart',
      label: 'cart.shipping.apart',
      isRequired: false,
      value: '',
      error: '',
    },
    {
      id: 'city',
      label: 'cart.shipping.city',
      isRequired: true,
      value: '',
      error: '',
    },
    {
      id: 'postal',
      label: 'cart.shipping.postal',
      isRequired: true,
      value: '',
      error: '',
    },
    {
      id: 'phone',
      label: 'cart.shipping.phone',
      isRequired: true,
      value: '',
      error: '',
    },
  ]);
  

  function changeInputHandler(index:number, e:ChangeEvent<HTMLInputElement>) {
    fields[index].value = e.target.value;
    fields[index].error = '';
    setFields([...fields]);
  }

  function changeEmailHandler(e:ChangeEvent<HTMLInputElement>) {
    errors.email = '';
    setErrors({...errors})
    setEmailInput(e.target.value)
  }
  
  function changeCountryHandler(e:DropdownChangeEvent) {
    errors.country = '';
    setErrors({...errors});
    setSelectedCountry(e.value);
  }

  function validate() {
    let isRightFields = true
    if (!emailInput.match(
      /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    )) {
      errors.email = 'cart.shipping.errorEmail';
      isRightFields = false;
    }
    if (selectedCountry === null) {
      errors.country = 'cart.shipping.error';
      isRightFields = false;
    }
    fields.forEach((item) => {
      if (item.id === 'phone' && !item.value.match(/^[\+]?[(]?[0-9]{3}[)]?[-\s\.]?[0-9]{3}[-\s\.]?[0-9]{4,6}$/im)) {
        item.error = ('cart.shipping.errorPhone')
      }
      if (item.value.trim() === '' && item.isRequired) {
        item.error = 'cart.shipping.error'
      }
    })
    setErrors({...errors})
    setFields([...fields])
    return isRightFields
  }
  
  function showModal() {
    let body = document.querySelector('body');
    setIsSuccess(true);
    if (body) {
      body.style.marginRight = window.innerWidth - body.clientWidth + 'px';
      body.classList.add('portal');
    }
  }

  function closeModal() {
    let body = document.querySelector('body');
    if (body) {
      body.style.marginRight = '0';
      body.classList.remove('portal');
    }
    navigate(PATHS.homepage)
  }

  async function formHandler(e:React.FormEvent<HTMLFormElement>) {
    e.preventDefault();
    if (validate()) {
      let isRightSub = false
      if (subRef.current?.checked) {
        try {
          setIsSubLoading(true);
          const responce = await fetch(`${url}subscribe?format=json&api_key=${apiKey}&list_ids=1&fields[email]=${emailInput.trim()}&double_optin=3&overwrite=0`, {
            method: 'POST',
          })
          const result = await responce.json()
          if (result.code==='invalid_arg') {
            errors.email = 'cart.shipping.errorEmail';
            setErrors({...errors})
          } else {
            isRightSub = true
          }
        } catch (error) {
          console.log(error)
        } finally {
          setIsSubLoading(false)
        }
      } else {
        isRightSub = true;
      }

      if (isRightSub) {
        setIsSendLoading(true)
        let productStr = `<div>Продукт: ${param.product}</div>`;
        let productPrice = `<div>Цена: ${t(product.price)}</div>`
        let email = `<div>Email: ${emailInput.trim()}</div>`;
        let country = '';
        if (selectedCountry) {
          country = `<div>Страна/регион: ${selectedCountry.name}</div>`;
        }
        let fieldsValue = '';
        fields.forEach((item) => fieldsValue += `<div>${t(item.label)}: ${item.value.trim()}</div>`)
        let bodyLetter = productStr + productPrice + email + country + fieldsValue;
        try {
          const responce = await fetch(noProxyUrl + `sendEmail?format=json&api_key=${apiKey}&email=russiaAG1@gmail.com&sender_name=Client&sender_email=russiaAG1@gmail.com&subject=order&body=${bodyLetter}&list_id=1&error_checking=1`);
          const result = await responce.json();
        } catch (error) {
          console.log(error)
        } finally {
          setIsSendLoading(false);
          showModal();
        }
      }
    }
  }

  return (
    <form className={style.shipping} onSubmit={isSendLoading || isSubLoading ? (e) => e.preventDefault() : formHandler}>
      <h2 className={style.shipping__heading}>{t('cart.shipping.heading')}</h2>
      <p className={style.shipping__text}>{t('cart.shipping.text')}</p>
      <span className={["p-float-label", style.shipping__inputBlock].join(' ')}>
        <InputText className={errors.email ? [style.shipping__input, style.shipping__inputError].join(' ') : style.shipping__input} id="email" value={emailInput} onChange={e => changeEmailHandler(e)}/>
        <label className={style.shipping__label} htmlFor="email">Email *</label>
        {errors.email && <div className={style.shipping__error}>{t(errors.email)}</div>}
      </span>
      <div className={style.shipping__sub}>
        <input ref={subRef} type="checkbox"  className={style.shipping__subInput} name="subscribe" id="sub" />
        <Icon className={style.shipping__icon} />
        <label htmlFor="sub" className={style.shipping__subLabel}>{t('cart.shipping.subscribe')}</label>
      </div>
      <span className={["p-float-label", style.shipping__inputBlock].join(' ')}>
        <Dropdown className={errors.country ? [style.shipping__input, style.shipping__inputError].join(' ') : style.shipping__input} inputId="country" value={selectedCountry} onChange={(e) => changeCountryHandler(e)} options={countries} optionLabel="name" />
        <label className={style.shipping__label} htmlFor="country">{t('cart.shipping.country')}</label>
        {errors.country && <div className={style.shipping__error}>{t(errors.country)}</div>}
      </span>
      {fields.map((item, index) => (
        <span key={index} className={["p-float-label", style.shipping__inputBlock].join(' ')}>
          <InputText id={item.id} onChange={(e) => changeInputHandler(index, e)} value={item.value} className={item.error ? [style.shipping__input, style.shipping__inputError].join(' ') : style.shipping__input} />
          <label className={style.shipping__label} htmlFor={item.id}>{t(item.label)}</label>
          {item.error && <div className={style.shipping__error}>{t(item.error)}</div>}
        </span>
      ))}
      <button type='submit' className={isSendLoading || isSubLoading ? [style.shipping__btn, style.shipping__btnLoad].join(' ') : style.shipping__btn}>
        {
          isSendLoading || isSubLoading ?
            <span className={style.loader}></span>
          :
            t('cart.shipping.btn') 
        }
      </button>
      {isSuccess && 
      createPortal(
        <div className={style.modal}>
          <div className={style.modal__modal}>
            <h3 className={style.modal__heading}>{t('cart.shipping.modal.heading')}</h3>
            <p className={style.modal__text}>{t('cart.shipping.modal.text')}</p>
            <button onClick={closeModal} className={style.modal__btn}>{t('cart.shipping.modal.btn')}</button>
          </div>
        </div>
      , document.body)
      }
    </form>
  )
}