import React, {useEffect} from 'react';
import Header from './Components/Header/Header';
import Homepage from './Pages/Homepage/Homepage';
import Footer from './Components/Footer/Footer';
import { Routes, Route, useLocation } from 'react-router';
import Cart from './Pages/Cart/Cart';
import Aboutpage from './Pages/Aboutpage/Aboutpage';

export const PATHS = {
  homepage: '/',
  cart: '/cart',
  about: '/about',
}

function App() {
  const routes = [
    {path: PATHS.homepage, element: <Homepage />, params: ''},
    {path: PATHS.cart, element: <Cart />, params: ':product'},
    {path: PATHS.about, element: <Aboutpage />, params: ''}
  ]
  const {pathname} = useLocation()
  useEffect(() => {
    window.scrollTo(0,0)
  },[pathname])

  return (
    <>
    <Header />
    <Routes>
      {routes.map( (route, index) => (
        route.params ?
          <Route key={index} path={route.path}><Route path={route.params} element={route.element} /></Route>
          :
          <Route key={index} path={route.path} element={route.element} />
      )
      )}
    </Routes>
    <Footer />
    </>
  );
}

export default App;
