import {useEffect, useState} from 'react';
import { useLocation } from 'react-router-dom';
import { PATHS } from '../../App';
import MainFooter from './MainFooter/MainFooter';

export default function Footer() {
  const location = useLocation();
  const [isCartpage, setIsCartpage] = useState(location.pathname.slice(0, PATHS.cart.length) === PATHS.cart);
  useEffect(() => {
    setIsCartpage(location.pathname.slice(0, PATHS.cart.length) === PATHS.cart)
  },[location.pathname])

  return (
    <>
    {isCartpage ?
    <footer></footer>
    :
    <MainFooter />}
    </>
  )
}