import style from './subscribe.module.scss';
import icon5 from './img/icon5.svg';
import { useTranslation } from 'react-i18next';
import {useState} from 'react'
import { apiKey, url } from '../../../../mail';

export default function Subscribe() {
  const {t} = useTranslation();
  const [inputValue, setInputValue] = useState('');
  const [error, setError] = useState('');
  const [isSuccess, setIsSuccess] = useState(false);
  const [isLoading, setIsLoading] = useState(false);

  function changeInput(e: React.ChangeEvent<HTMLInputElement>) {
    setInputValue(e.target.value);
    setError('');
  }
  
  async function submitHandler(e: React.FormEvent<HTMLFormElement>) {
    e.preventDefault();
    if (inputValue.match(
      /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    )) {
      try {
        setIsLoading(true);
        const responce = await fetch(`${url}subscribe?format=json&api_key=${apiKey}&list_ids=1&fields[email]=${inputValue}&double_optin=3&overwrite=0`, {
          method: 'POST',
        })
        const result = await responce.json()
        if (result.code==='invalid_arg') {
          setError('footer.connect.error')
        } else {
          setIsSuccess(true);
        }
      } catch (error) {
        console.log(error)
      } finally {
        setIsLoading(false)
      }
    } else {
      setError('footer.connect.error')
    }
  }

  return (
    <>
      <form onSubmit={isSuccess || isLoading ? (e) => e.preventDefault() : submitHandler} className={style.inputBlock} action="">
        <input className={error ? [style.input, style.inputError].join(' ') : style.input} onChange={changeInput} value={inputValue} placeholder={t('footer.connect.input')} type="text" />
        {isLoading ?
          <span className={style.loader}></span>
        :
          <button type='submit' className={style.btn}>
            <img src={icon5} alt="arrow" />
          </button>}
        {isSuccess && <div className={style.success}>{t('footer.connect.success')}</div>}
      </form>
      {error && <div className={style.error}>{t(error)}</div>}
    </>
  )
}