import { useTranslation } from 'react-i18next';
import style from './believe.module.scss';

export default function BelieveSection() {
  const {t} = useTranslation();

  return (
    <section className={style.section}>
      <div className="container">
        <p className={style.believe}>{t('aboutpage.believeSection.believe')}</p>
        <p className={style.text}>{t('aboutpage.believeSection.text')}</p>
      </div>
    </section>
  )
}