import { NavigateFunction, useNavigate } from "react-router";
import packImg from './img/pack.png';
import pouchImg from './img/pouch.png';
import extraImg1 from './img/extra1.png';
import extraImg2 from './img/extra2.png';
import bottle1Img from './img/1bottle.png';
import bottle2Img from './img/2bottle.png';
import { PATHS } from "../../App";

export function getProduct(product: string | undefined, navigate:NavigateFunction) {
  switch(product) {
    case 'pouch1':
      return {
        product1: {
          img: pouchImg,
          heading: 'cart.order.pouch1.heading1',
          text: 'cart.order.pouch1.text'
        },
        product2: {
          img: bottle1Img,
          heading: 'cart.order.pouch1.heading2',
        },
        price: 'cart.order.pouch1.price',
        mobile: 'cart.order.pouch1.mobile',
        list: ['cart.order.pouch1.list.item1', 'cart.order.pouch1.list.item2'],
      };
    case 'pouch2':
      return {
        product1: {
          img: pouchImg,
          heading: 'cart.order.pouch2.heading1',
          text: 'cart.order.pouch2.text'
        },
        product2: {
          img: extraImg1,
          heading: 'cart.order.pouch2.heading2',
        },
        price: 'cart.order.pouch2.price',
        mobile: 'cart.order.pouch2.mobile',
        list: ['cart.order.pouch2.list.item1', 'cart.order.pouch2.list.item2','cart.order.pouch2.list.item3','cart.order.pouch2.list.item4'],
      };
    case 'pouch3':
      return {
        product1: {
          img: pouchImg,
          heading: 'cart.order.pouch3.heading1',
          text: 'cart.order.pouch3.text',
        },
        product2: {
          img: extraImg2,
          heading: 'cart.order.pouch3.heading2',
        },
        price: 'cart.order.pouch3.price',
        mobile: 'cart.order.pouch3.mobile',
        list: ['cart.order.pouch3.list.item1', 'cart.order.pouch3.list.item2', 'cart.order.pouch3.list.item3', 'cart.order.pouch3.list.item4'],
      };
    case 'pack1':
      return {
        product1: {
          img: packImg,
          heading: 'cart.order.pack1.heading1',
          text: 'cart.order.pack1.text'
        },
        product2: {
          img: bottle1Img,
          heading: 'cart.order.pack1.heading2',
        },
        price: 'cart.order.pack1.price',
        mobile: 'cart.order.pack1.mobile',
        list: ['cart.order.pack1.list.item1', 'cart.order.pack1.list.item2'],
      };
    case 'pack2':
      return {
        product1: {
          img: packImg,
          heading: 'cart.order.pack2.heading1',
          text: 'cart.order.pack2.text'
        },
        product2: {
          img: bottle1Img,
          heading: 'cart.order.pack2.heading2',
        },
        price: 'cart.order.pack2.price',
        mobile: 'cart.order.pack2.mobile',
        list: ['cart.order.pack2.list.item1', 'cart.order.pack2.list.item2','cart.order.pack2.list.item3','cart.order.pack2.list.item4'],
      };
    case 'pack3':
      return {
        product1: {
          img: packImg,
          heading: 'cart.order.pack3.heading1',
          text: 'cart.order.pack3.text'
        },
        product2: {
          img: bottle2Img,
          heading: 'cart.order.pack3.heading2',
        },
        price: 'cart.order.pack3.price',
        mobile: 'cart.order.pack3.mobile',
        list: ['cart.order.pack3.list.item1', 'cart.order.pack3.list.item2','cart.order.pack3.list.item3','cart.order.pack3.list.item4'],
      };
      default: 
        navigate(PATHS.homepage);
        return {
          product1: {
            img: '',
            heading: '',
            text: ''
          },
          product2: {
            img: '',
            heading: '',
          },
          price: '',
          mobile: '',
          list: [''],
        };
    }
}
