import style from './cart.module.scss';
import { useNavigate, useParams } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { getProduct } from './content';
import Order from './Order/Order';
import Shipping from './Shipping/Shipping';

export default function Cart() {
  const {t} = useTranslation();
  const param = useParams();
  const navigate = useNavigate();
  const product = getProduct(param.product, navigate);

  return (
    <main className='cart'>
      <div className={style.container}>
        <div className={style.mobile}>
          <div className={style.mobile__img}>
            <img src={product.product1.img} alt="product" />
          </div>
          <h2 className={style.mobile__heading}>{t(product.mobile)}</h2>
          <div className={style.mobile__price}>{t(product.price)}</div>
          <ul className={style.mobile__list}>
            {product.list.map((item, index) => (
              <li key={index} className={style.mobile__list}>{t(item)}</li>
            ))}
          </ul>
        </div>
        <Shipping />
        <Order />
      </div>
    </main>
  )
}