import { createSlice } from '@reduxjs/toolkit'

const initialState = {
  isPouch: true,
}

export const isPouchSlice = createSlice({
  name: 'isPouch',
  initialState,
  reducers: {
    setIsPouch: (state, action) => {
      state.isPouch = action.payload;
    },
  },
})

// Action creators are generated for each case reducer function
export const { setIsPouch} = isPouchSlice.actions

export default isPouchSlice.reducer