import style from './order.module.scss';
import { useNavigate, useParams } from 'react-router';
import { useTranslation } from 'react-i18next';
import { getProduct } from '../content';

export default function Order() {
  const {t} = useTranslation();
  const navigate = useNavigate();
  const param = useParams();
  const product = getProduct(param.product, navigate);
  
  
  return (
    <div className={style.order}>
      <h3 className={style.order__heading}>{t('cart.order.heading')}</h3>
      <div className={style.order__products}>
        <div className={style.order__product}>
          <div className={style.order__productImg}>
            <img src={product.product1.img} alt="product" />
          </div>
          <div className={style.order__productDesc}>
            <h4 className={style.order__productHeading}>{t(product.product1.heading)}</h4>
            <p className={style.order__productText}>{t(product.product1.text)}</p>
          </div>
          <div className={style.order__productPrice}>{t(product.price)}</div>
        </div>
        <div className={style.order__product}>
          <div className={style.order__productImg}>
            <img src={product.product2.img} alt="product" />
          </div>
          <div className={style.order__productDesc}>
            <h4 className={style.order__productHeading}>{t(product.product2.heading)}</h4>
          </div>
          <div className={style.order__productPrice}>{t('cart.order.free')}</div>
        </div>
      </div>

      <div className={style.order__positions}>
        <div className={style.order__position}>
          <div className={style.order__positionItem}>{t('cart.order.subtotal')}</div>
          <div className={style.order__positionItem}>{t(product.price)}</div>
        </div>
        <div className={style.order__position}>
          <div className={style.order__positionItem}>{t('cart.order.shipping')}</div>
          <div className={style.order__positionItem}>{t('cart.order.free')}</div>
        </div>
      </div>
      <div className={style.order__total}>
        <div className={style.order__totalItem}>{t('cart.order.total')}</div>
        <div className={style.order__totalItem}>{t(product.price)}</div>
      </div>
    </div>
  )
}