import { useTranslation } from 'react-i18next';
import style from './partnerSection.module.scss';
import img11 from './img/img1.webp';
import img12 from './img/img1.jpg';
import img21 from './img/img2.webp';
import img22 from './img/img2.jpg';
import img31 from './img/img3.webp';
import img32 from './img/img3.jpg';

export default function PartnerSection() {
  const {t} = useTranslation();
  const content = [
    {
      heading: 'aboutpage.partnerSection.item1.heading',
      text: 'aboutpage.partnerSection.item1.text',
      img1: img11,
      img2: img12,
    },
    {
      heading: 'aboutpage.partnerSection.item2.heading',
      text: 'aboutpage.partnerSection.item2.text',
      img1: img21,
      img2: img22,
    },
    {
      heading: 'aboutpage.partnerSection.item3.heading',
      text: 'aboutpage.partnerSection.item3.text',
      img1: img31,
      img2: img32,
    },
  ]

  return (
    <section className={style.section}>
      <div className="container">
        <h2 className={style.heading}>{t('aboutpage.partnerSection.heading')}</h2>
        <div className={style.items}>
          {content.map((item, index) => (
            <div key={index} className={style.item}>
              <div className={style.img}>
                <picture>
                  <source src={item.img1} type='image/webp'/>
                  <img src={item.img2} alt="partner" loading='lazy' decoding='async'/>
                </picture>
              </div>
              <div className={style.content}>
                <h3 className={style.itemHeading}>{t(item.heading)}</h3>
                <p className={style.text}>{t(item.text)}</p>
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  )
}